#technical-panels {
    position: fixed;
    top: 88px;

    height: fit-content;
    width: calc(75vw - 120px);
    left: calc(15vw + 64px);
    z-index: 3;
}

.title {
    margin: 0;
    margin-bottom: -7px;
    color: black;
    font-size: 22px;
    font-family: 'pixel',Lato,LatoExtended,sans-serif;
    white-space: nowrap;
    overflow: hidden;
    width: 98%;
    font-weight: bold;
    -webkit-font-smoothing: none;
    position: relative;
    display: inline-block;
    padding: 5px 0 1px 2%;
    border-bottom: 4px solid #f1f1f1;
    z-index: 2;
}

_::-webkit-full-page-media, _:future, :root .title {
    font-size: 24px;
    line-height: 1.1;
    letter-spacing: -1px;
}

.title:hover {
    cursor: pointer;
    color: rgb(31, 33, 180);
    border-color: rgb(142, 144, 255);
    background-color: rgba(21, 144, 206, 0.075);
}

_::-webkit-full-page-media, _:future, :root #greeting-text {
    -webkit-font-smoothing: subpixel-antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.info-panel-content hr {
    width: 100%;
    margin: 0;
    margin-top: 4px;
    margin-bottom: 8px;
    height: 4px;
    background: #e5e5e5;
    border: none;
}

.info-panel-content #panel-text {
    font-size: 19px;
    text-align: left;
    font-weight: bold;
    font-family: 'pixel',Lato,LatoExtended,sans-serif;
    line-height: 1.2;
    width: 90%;
    padding-left: 5%;
    padding-right: 5%;
    -webkit-font-smoothing: none;
}

_::-webkit-full-page-media, _:future, :root .info-panel-content #panel-text {
    font-size: 22px;
    line-height: 1.1;
    letter-spacing: -1px;
}

.tech-info-panel {
    margin-bottom: 16px;
    margin-top: 8px;
    width: calc(75vw - 120px);
    position: relative;
    animation: fade-in 1.5s linear;
    transform-origin: bottom left;

    border: 4px solid black;
    border-width: 0px 4px;

    display: block;
    color: black;
    z-index: 1;
    justify-content: space-between;
    flex-wrap: wrap;
    background-color: rgba(255, 255, 255, 0.9875);
}

@keyframes fade-in {
    from { opacity: 0; }
    to { opacity: 1; }
}

.tech-info-panel .sides {
    top: -4px;
    right: 0;
    width: 100%;
    height: 100%;

    position: absolute;
    z-index: 3;
    pointer-events: none;

    border: 4px solid black;
    border-width: 4px 0px;
}

.info-panel-content-container {
    margin-left: 2%;
    margin-right: 2%;
    position: relative;
    overflow-y: hidden;
    height: fit-content;
    margin-top: 0px;
}

.info-panel-content {
    position: absolute;
    bottom: 0;
}

_::-webkit-full-page-media, _:future, :root .info-text {
    -webkit-font-smoothing: subpixel-antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.tech-info-panel .corner {
    position: absolute;
    background-image: url(./Sprites/corner-light.png);
    width: 12px;
    height: 12px;
    image-rendering: pixelated;
    z-index: 3;
}

.tech-info-panel #top-right-corner {
    background-image: url(./Sprites/corner-light-compact.png);
    width: 4px;
    height: 4px;
    transform: rotate(0deg);
    right: 0px;
    top: 0px;
}

.tech-info-panel #bottom-right-corner {
    transform: rotate(90deg);
    right: 0px;
    bottom: 0px;
}

.tech-info-panel #bottom-left-corner {
    transform: rotate(180deg);
    left: 0;
    bottom: 0;
}

.tech-info-panel #top-left-corner {
    background-image: url(./Sprites/corner-light-compact.png);
    width: 4px;
    height: 4px;
    transform: rotate(270deg);
    left: 0;
    top: 0;
}

@media (max-width: 1000px) {
    #technical-panels {
        width: calc(85vw - 120px);
        left: calc(7.5vw + 64px);
    }

    .tech-info-panel {
        width: calc(85vw - 120px);
    }
}

@media (max-width: 900px) {
    /* .info-panel-content #panel-text {
        font-size: 16pt;
    } */
}

@media (max-width: 850px) {
    #technical-panels {
        width: calc(92.5vw - 120px);
        left: calc(3.75vw + 64px);
    }

    .tech-info-panel {
        width: calc(92.5vw - 120px);
    }
}

@media (max-width: 750px) {
    #technical-panels {
        width: calc(100vw - 40px);
        left: calc(16px);
    }

    .tech-info-panel {
        width: calc(100vw - 40px);
    }
}

@media (max-width: 525px) {
    .title {
        font-size: 16pt;
    }
}

