#rocket-guy {
    transform-origin: top left;
    position: fixed;
    transform-origin: left;
    top: 0px;
    zoom: 1;
    width: 80px;
    height: 80px;
    transition: transform 600ms ease-out;
    z-index: 2;
    animation-name: float;
    animation-timing-function: steps(5);
    animation-duration: 1s;
    animation-iteration-count: infinite;
}

.rocket-guy-home {
    left: calc(15vw - 40px);
}

.rocket-guy-technical {
    left: calc(15vw - 40px);
}

/* .rocket-guy-extra {
    display: none;
} */

.sprite {
    image-rendering: optimizeSpeed;     
    image-rendering: -moz-crisp-edges;          
    image-rendering: -o-crisp-edges;       
    image-rendering: -webkit-optimize-contrast;
    image-rendering: pixelated; 
    image-rendering: optimize-contrast;      
    -ms-interpolation-mode: nearest-neighbor;
    image-rendering: auto;
    image-rendering: crisp-edges;
    image-rendering: pixelated;
    filter: none;
}

.idle {
    background-image: url(./AnimationSprites/idle.png);
}

@keyframes float {
    from { translate: 0 0; }
    50% { translate: 0 5px; }
    to { translate: 0 0; }
}

.fly-up-build {
    background-image: url(./AnimationSprites/fly-up-build.png);
}

.fly-up-sustain {
    background-image: url(./AnimationSprites/fly-up-sustain.png);
}

.fly-up-stop {
    background-image: url(./AnimationSprites/fly-up-stop.png);
}

.fly-down-build {
    background-image: url(./AnimationSprites/fly-down-build.png);
}

.fly-down-sustain {
    background-image: url(./AnimationSprites/fly-down-sustain.png);
}

.fly-down-stop {
    background-image: url(./AnimationSprites/fly-down-stop.png);
}

.hit-ceiling {
    background-image: url(./AnimationSprites/hit-ceiling.png);
}

.landing {
    background-image: url(./AnimationSprites/landing.png);
}

.take-off {
    background-image: url(./AnimationSprites/take-off.png);
}

.fly-up-build-switch-mid {
    background-image: url(./AnimationSprites/fly-up-build-switch-mid.png);
}

.fly-down-build-switch-mid {
    background-image: url(./AnimationSprites/fly-down-build.png);
}

@media (max-width: 1000px) {
    .rocket-guy-home {
        left: 7.5vw;
    }

    .rocket-guy-technical {
        left: calc(10vw - 40px);
    }
}

@media (max-width: 850px) {
    .rocket-guy-technical {
        left: calc(7vw - 40px);
    }
}

@media (max-width: 800px) {
    .rocket-guy-home {
        left: 5vw;
    }
}

@media (max-width: 750px) {
